.cmplz-hidden {
  display: none;
}

.cmplz-logo-container {
  display:flex;
  gap:40px;
}

.cmplz-input-group {
  min-width:250px;
}

#complianz .cmplz-cookiebanner-preview-controls {
  margin-left:10px;
  ul {
	margin-bottom:15px;
	li {
	  display: flex;
	  cursor: pointer;
	  margin: 5px;

	  .cmplz-icon {
		margin-right: 10px;
	  }

	  &.inactive {
		text-decoration: underline;
	  }

	  &.loading {
		color:var(--rsp-grey-400);
	  }

	}
  }
}

.cmplz-banner-reset-button {
  button.button.button-default {
	display: flex;
	.cmplz-icon {
	  margin: auto auto auto 10px;
	}
  }
}

.cmplz-logo-preview {
  &.cmplz-clickable {
	cursor:pointer;
	max-width:90px;
	height:inherit;
  }
  &.cmplz-complianz-logo {
	padding: 5px;
	svg {
	  height: 90px;
	  width: 90px;
	}
  }
  border: 1px black dashed;
  display: flex;
  align-items: center;
  justify-content: center;
  float: right;
}

/**
Preview to the right
 */


#cmplz-cookiebanner-container {
  .cmplz-cookiebanner.reloading {
	//opacity: 0.7;
  }
  .cmplz-cookiebanner.cmplz-bottom {
	margin-right:20px;
	width:calc( 100% - 50px );
  }

  .cmplz-cookiebanner.cmplz-bottom-left, .cmplz-cookiebanner.cmplz-bottom-right {
	left: initial;
	right: 0;

	@media (max-width: 1800px) {
	  transform: translateX( calc(0.3 * var(--cmplz_banner_width)) );

	  &:hover {
		animation: slideBannerIn 1s forwards;
		animation-delay: 0s;
	  }
	}
  }


  .cmplz-cookiebanner.cmplz-center {
	right:0;
	left: initial;
	transform: initial;
	//this causes issues with the fade in center variant
	top:initial;
	bottom:50px;
	@media (max-width:1800px) {
	  transform: translateX( calc(0.3 * var(--cmplz_banner_width)) );

	  &:hover {
		animation: slideBannerIn 1s forwards;
		animation-delay: 0s;
	  }
	}

  }


  @-webkit-keyframes slideBannerIn {
	100% {
	  transform: inherit;
	}
  }

  @keyframes slideBannerIn {
	100% {
	  transform: inherit;
	}
  }

}

